import React, { useState } from "react";
import "./MainCategoriesCards.css";

import { FaCalendar } from "react-icons/fa";

import arrow_left from "./arrow-left.png";
import arrow_right from "./arrow-right-to-left.png";

import triangle1 from "./triangle1.png";
import triangle2 from "./triangle2.png";

import { Link } from "react-router-dom";

export default function MainCategoriesCards(props) {
  const [hoverCard1, setHoverCard1] = useState(false);

  const handleMouseEnterOne = () => {
    setHoverCard1(true);
  };

  const handleMouseLeaveOne = () => {
    setHoverCard1(false);
  };

  const [hoverCard2, setHoverCard2] = useState(false);

  const handleMouseEnterTwo = () => {
    setHoverCard2(true);
  };

  const handleMouseLeaveTwo = () => {
    setHoverCard2(false);
  };

  const [hoverCard3, setHoverCard3] = useState(false);

  const handleMouseEnterThree = () => {
    setHoverCard3(true);
  };

  const handleMouseLeaveThree = () => {
    setHoverCard3(false);
  };

  const [hoverCard4, setHoverCard4] = useState(false);

  const handleMouseEnterFour = () => {
    setHoverCard4(true);
  };

  const handleMouseLeaveFour = () => {
    setHoverCard4(false);
  };

  return (
    <div className="main-categories-cards-main-container">
      <div className="main-categories-cards-main-container-row-1">
        <Link
          to={props.card1Link}
          className="main-categories-cards-main-container-row-1-card-1"
          onMouseEnter={handleMouseEnterOne}
          onMouseLeave={handleMouseLeaveOne}
        >
          {/* <FaCalendar
            size={50}
            color={hoverCard1 ? "#fff" : "#612d91"}
            className="calendar"
          /> */}

          <img
            src={hoverCard1 ? props.cardOneImageWhite : props.cardOneImagePurple}
            className="main-categories-cards-main-container-row-1-card-1-icon"
            alt="card-1"
          />

          <br />

          <h1>{props.card1heading}</h1>

          <br />

          <p>{props.card1paragraph}</p>

          <img src={arrow_left} className="arrow-1" alt="an-arrow" />

          <img src={triangle1} className="triangle-1" alt="triangle 1" />
        </Link>

        <Link
          to={props.card2Link}
          className="main-categories-cards-main-container-row-1-card-2"
          onMouseEnter={handleMouseEnterTwo}
          onMouseLeave={handleMouseLeaveTwo}
        >
          <img
            src={hoverCard2 ? props.cardTwoImageWhite : props.cardTwoImagePurple}
            className="main-categories-cards-main-container-row-1-card-1-icon"
            alt="card-1"
          />

          <br />

          <h1>{props.card2heading}</h1>

          <br />

          <p>{props.card2paragraph}</p>

          <img src={arrow_right} className="arrow-2" alt="an-arrow" />

          <img src={triangle2} className="triangle-2" alt="triangle 2" />
        </Link>

        <Link
          to={props.card3Link}
          className="main-categories-cards-main-container-row-1-card-3"
          onMouseEnter={handleMouseEnterThree}
          onMouseLeave={handleMouseLeaveThree}
        >
          <img
            src={hoverCard3 ? props.cardThreeImageWhite : props.cardThreeImagePurple}
            className="main-categories-cards-main-container-row-1-card-1-icon"
            alt="card-1"
          />

          <br />

          <h1>{props.card3heading}</h1>

          <br />

          <p>{props.card3paragraph}</p>

          {props.numberOfCards > 3 && (
            <img src={arrow_left} className="arrow-1" alt="an-arrow" />
          )}

          <img src={triangle1} className="triangle-1" alt="triangle 1" />
        </Link>

        {props.numberOfCards > 3 && (
          <Link
            to={props.card4Link}
            className="main-categories-cards-main-container-row-1-card-4"
            onMouseEnter={handleMouseEnterFour}
            onMouseLeave={handleMouseLeaveFour}
          >
            <FaCalendar
              size={50}
              color={hoverCard4 ? "#fff" : "#612d91"}
              className="calendar"
            />

            <br />

            <h1>{props.card4heading}</h1>

            <br />

            <p>{props.card4paragraph}</p>

            {props.numberOfCards > 4 && (
              <img src={arrow_right} className="arrow-2" alt="an-arrow" />
            )}

            <img src={triangle2} className="triangle-2" alt="triangle 2" />
          </Link>
        )}

        {props.numberOfCards > 4 && (
          <Link
            to={props.card5Link}
            className="main-categories-cards-main-container-row-1-card-3"
            onMouseEnter={handleMouseEnterThree}
            onMouseLeave={handleMouseLeaveThree}
          >
            <FaCalendar
              size={50}
              color={hoverCard3 ? "#fff" : "#612d91"}
              className="calendar"
            />

            <br />

            <h1>{props.card5heading}</h1>

            <br />

            <p>{props.card5paragraph}</p>

            <img src={triangle1} className="triangle-1" alt="triangle 1" />
          </Link>
        )}
      </div>

      <div className="main-categories-cards-main-container-row-2">
        <div className="main-categories-cards-main-container-row-2-pic1">
          <img src={props.pic1} alt="pic 20 here" style={{ width: "80%" }} />
        </div>

        <div className="main-categories-cards-main-container-row-2-pic2">
          <img src={props.pic2} alt="pic 20 here" style={{ width: "80%" }} />
        </div>
      </div>
    </div>
  );
}
